import React from "react";

function Img({ id }) {
  // const img = `./Posters/${id}.jpg`;
  // const img = URL.createObjectURL(`http://${window.endPoint}/storage/img/${id}.jpg`);
  const posterStyle = {
    position: "absolute",
    top: 0,
    left: 0,
    width: 100 + "%",
    height: 100 + "%",
    backgroundImage: `url('http://${window.endPoint}/storage/img/${id}.jpg')`,
    backgroundSize: 100 + "%",
    backgroundRepeat: "no-repeat"
  };
  return <div style={posterStyle} />;
}
export default Img;
